<template>
  <div>
    <el-form ref="form" :model="formData" label-width="80px">
      <el-form-item label="姓名">
        <el-input v-model="formData.username"></el-input>
      </el-form-item>
      <el-form-item label="用户名">
        <el-input v-model="formData.idCard"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input type="password" v-model="formData.pwd"></el-input>
      </el-form-item>
      <el-form-item label="用户分类">
        <el-select v-model="formData.branchId" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item._id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="success" @click="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>

  export default {
    name: "add",
    components: {
    },
    data() {
      return {
        formData: {
          username: '',
          idCard: '',
          pwd: '',
          branchName: '',
          branchId: ''
        },
        options:[]
      }
    },
    methods: {
      getcategory(){
        this.$axios.post('getuserscate').then(res=>{
          this.options = res.data
          // console.log(this.options)
        })
      },
      // getData(){
      //   // forEach(val in this.options,function () {
      //   //   if(val._id=this.formData.branchId) {
      //   //     console.log(val.label)
      //   //   }
      //   // })
      //   console.log(this.options)
      //   for(var i=0;i<this.options.length;i++){
      //     if(this.options[i]._id=this.formData.branchId){
      //       console.log(this.options[i].label)
      //       this.formData.branchName = this.options[i].label;
      //     }
      //   }
      //
      // },
      submit(){
        this.options.forEach(val => {
          if(val._id == this.formData.branchId){
            this.formData.branchName = val.label;
          }
        })
        // this.getData()
        this.$axios.post("addusers",this.formData).then((res)=>{
          if(res.code==200){
            this.$message({
              type: 'success',
              message: '提交成功!'
            });
          }else{
            this.$message({
              type: 'warning',
              message: res.msg
            });
          }
          setTimeout(()=>{
            this.$router.push("/crmusers")
          },1000)
        })

      }
    },
    created() {
      this.$nextTick(()=>{
        this.getcategory()
        // this.getData()

      })

    }
    // watch:{
    //   $route(to,from,next){
    //     this.formData = {
    //       title: '',
    //       author: '',
    //       img: '',
    //       content: "",
    //       contentText: ''
    //     }
    //   }
    // }
  }
</script>
<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }

  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>
<style scoped>

</style>



